.carrito {
  min-height: 400px;
  min-width: 300px;
  overflow-x: scroll;
}

.header-logo {
  display: flex;
  justify-content: center;
}

.header-contacto {
  display: flex;
  justify-content: space-around;
}

.header-contacto li {
  list-style-type: none;
}
.header-contacto li .submenu {
  list-style-type: none;
}

.header-Buscar {
  display: flex;
  justify-content: space-evenly;
}

.header-Buscar input {
  width: 40%;
  border-radius: 10px;
}

/*---------OPCIONES---------*/
.opciones {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.linea-opciones-animacion {
  display: inline-block;
  position: relative;
  color: black;
}

.linea-opciones-animacion:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: black;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.linea-opciones-animacion:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/*--dropdown*/
li a, .dropbtn {
  display: inline-block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li.dropdown {
  display: inline-block;
  color: black;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 99;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: center;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
  color: black;
}

.dropdown a:hover {
  color: black;
}

/*----------body-----------*/
main {
  display: flex;
  flex-direction: column;
}

main section {
  display: flex;
}

/*-----------footer-----------*/
footer {
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #4b3d3c;
}

.footer-Top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-bottom {
  color: white;
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 5rem;
}

.footer-redesSociales {
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  display: flex;
  justify-content: center;
  margin: 5px 5px 5px 5px;
  text-decoration: none;
}

.footer-redesSociales a img {
  width: 20px;
  height: 20px;
  margin-bottom: 4px;
}

.footer-redesSociales-circulo {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  color: #fff;
  text-align: center;
  font-size: 18px;
  width: 42px;
  height: 42px;
  text-decoration: none;
  padding: 17px 18px 14px 18px;
  transition: 0.2s;
  transition-timing-function: ease-in-out;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-redesSociales-circulo:hover {
  border: 1px solid white;
}

/*Responsive de Tablet*/
@media screen and (max-width: 991.98px) {
  footer {
    width: 100%;
    height: auto;
    display: flex;
  }
  .footer-bottom {
    display: flex;
    flex-direction: column;
  }
  .footer-bottom div ul {
    padding: 0;
  }
}
/*Responsive de Celular*/
@media screen and (max-width: 576px) {
  .header-logo img {
    width: 80px;
  }
  .header-Buscar {
    order: 1;
  }
  .header-contacto {
    display: none;
  }
  .opciones {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  /*------Footer------*/
  footer {
    height: 50px;
    width: 100%;
    display: flex;
    text-align: center;
    background-color: #939482;
  }
  .footer-Top {
    display: none;
  }
  .footer-bottom {
    justify-content: center;
    width: 100%;
  }
  .footer-bottom div ul {
    display: none;
  }
}
.footer {
  padding: 2.5rem;
  margin-bottom: 0;
}

